import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Courses from '@/components/Courses'
import Login from '@/components/Login'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/Main',
        name: 'Main',
        component: Main
    }, {
        path: '/Courses',
        name: 'Courses',
        component: Courses
    }, {
        path: '/Login',
        name: 'Login',
        component: Login
    }, {
        path: '/',
        redirect: '/Login'
    }]
})


