<template>
    <div class="main">
          <el-main>
            <div class="details">
              <div class="sign">
                <div class="clearfix">
                  <div class="block" id="kaori" style="display: inline-block; ">
                    <span class="demonstration">选择班级：</span>
                    <el-cascader
                      ref="myCascader"
                      v-model="value"
                      :options="options"
                      :props="{ expandTrigger: 'hover' }"
                      onchange="handleChange"
                    ></el-cascader>
                  </div>
                  <div class="course" style="display: inline-block;">
                    <span>课程：</span>
                    <el-select v-model="value1" placeholder="请选择课程">
                      <el-option
                        v-for="item in options1"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </div>
                  <div class="time" style="display: inline-block;">
                    <span>日期:</span>
                    <el-date-picker
                      v-model="value"
                      type="date"
                      value-format="yyyy-MM-dd"
                      placeholder="选择日期"
                      :picker-options="pickerOptions"
                    ></el-date-picker>
                  </div>
                  <div class="teacher" style="display: inline-block;">
                    <span>教师：{{teacher}}</span>
                  </div>
                </div>

              
              <div class="table clearfix">
                <el-table
                  :data="tableData2"
                  :row-style="todo"
                  :highlight-current-row="true"
                  height="500"
                  border
                  style="width: 100%;"
                >
                  <el-table-column prop="date" label="日期" width="200"></el-table-column>
                  <el-table-column prop="name" label="姓名" width="200"></el-table-column>
                  <el-table-column prop="number" label="学号" width="200"></el-table-column>
                  <el-table-column prop="state" label="课程" width="200"></el-table-column>
                  <el-table-column prop="time" label="签到时间" width="200"></el-table-column>
                  <el-table-column prop="position" label="签退时间" width="180"></el-table-column>
                  <el-table-column prop="status" label="课程状态"></el-table-column>
                </el-table>
              </div> 
                <!--
                <div class="tableWrap clearfix">
                  <div
                    v-bind:style="{ color: activeColor}"
                    class="item"
                    v-for="(item,index) in items"
                    :key="index"
                  >
                    这里能改成像表格一样的话就好啦
                    <div class="name">{{item.name}}</div>
                    <div
                      v-for="(aitem,aindex) in item.details"
                      :key="aindex"
                      class="itemdetail"
                    >{{aitem.title}}</div>
                  </div>
                </div>-->
              </div>
            </div>
          </el-main>
        </div>
</template>
<script>
import {Students} from '../utils/students'
import {Classes} from '../utils/classes'
import {Courses} from '../utils/courses'
export default {
  name: "Courses",
  created() {
    this.tableData2 = Students
    this.options = Classes
    this.options1 = Courses
    this.init()
  },
  destroyed() {
    //销毁监听
    this.socket.onclose = this.close
  },
  methods: {
    todo({row,rowIndex}) {
      if (rowIndex === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      },
    handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
    init: function() {
      if(typeof(WebSocket) === "undefined") {
        alert("您的浏览器不支持socket")
      }else {
        //实例化socket
        this.socket = new WebSocket('ws://localhost:8088')
        //监听socket连接
        this.socket.onopen = this.open
        //监听socket错误信息
        this.socket.onerror = this.error
        //监听socket消息
        this.socket.onmessage = this.getMessage
      }
    },
    open:function() {
      console.log("socket连接成功")
      this.tableData2[0].name = 'kaori kasai'
    },
    error: function() {
      console.log("连接错误")
    },
    getMessage: function(msg) {
      console.log("<========================================================>")
      console.log(msg+"hhhhhhhhhhhhhhhhh")
    },
    send: function() {
      this.socket.send("hdhhhhh")
    },
    close: function() {
      console.log("socket已经关闭")
    },
    rowClass: function(row,index) {
      if (index === 1) {
        return {"background-color":"warning-row"};
      } else if (index === 3) {
        return {"background-color":"success-row"};
      }
      return "";
    },
    changeColor: function(status) {
      console.log(status)
      
      //  console.log()
      //  console.log("=====================================" + String(status))
      //   if (status === "正常") {
      //     return {"background-color":"green"}
      //   }
      return {"background-color":"blue"}
    },
    pickerOptions: function() {}
  },
  data: function() {
    return {
      socket: "",
      teacher: "kaori kasai",
      activeColor: "red",
      value: [],
      tableData2:[],
      options: [],
      options1: [],
      value1: ""
    };
  }
};
</script>
<style scoped>
#kaori {
  float: left;
}

.course {
  float: left;
  padding-left: 70px;
}

.time {
  float: left;
  padding-left: 70px;
}

.teacher {
  float: left;
  padding-left: 80px;
  margin-top: 10px;
}
.clearfix::after {
  content: "";
  display: block;
  clear: both;
  padding-bottom: 20px;
}

.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}

.tableWrap {
  margin-top: 50px;
  border: 1px solid #333;
  height: 500px;
  width: 100%;
}

.tableWrap .item {
  width: 100px;
  border-right: 1px solid #333;
  float: left;
  height: 100%;
}
.tableWrap .item .name {
  height: 60px;
  line-height: 60px;
  text-align: center;
  border-bottom: 1px solid #333;
}
.tableWrap .item .itemdetail {
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #333;
}
.el-table__body tr.current-row>td {
  background: rgba(249, 185, 185, 0.897)!important;
}
</style>


